.profile {
  width: 400px;
  margin: 45px;
  margin-bottom: 100px;
  display: flex;
  background-color: #989899;
  flex-direction: column;
  align-items: center;
  border-radius: 12px;
  text-align: center;
  padding: 20px;
}

.description {
}

.avatarImg {
  background-color: #c8bcbbeb;
  width: 286px;
  border-radius: 50%;
  padding-top: 10px;
  margin-bottom: 30px;
}

.name {
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 10px;
}

.tag {
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 10px;
}

.location {
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 50px;
}

.stats {
  display: flex;
  gap: 55px;
}

.statsItem {
  display: flex;
  flex-direction: column;
}
.label {
  font-size: 20px;
}

.quantity {
  font-size: 20px;
  font-weight: 700;
}
